const { createApp } = require('vue')
const VendorPortal = require('../views/VendorPortal.vue').default
const { sharedPinia } = require('../store/shared')
require('../assets/styles.css')

const app = createApp(VendorPortal, {
  props: {
    token: new URLSearchParams(window.location.search).get('token') || 
           window.location.pathname.split('/').pop()
  }
})

app.use(sharedPinia)

app.mount('#app')
